@import 'variables.scss';

header {
  grid-template: "header";
  display: flex;
  height: 300px;
  align-items: center;

  h1.title {
    color: $off-white;
  }
}


.details {
  display: flex;
  flex-direction: row;
  .images {
    display: flex;
    flex-wrap: wrap;
    width: 60%;
      img {
        width: 100%;
        object-fit: cover;
    }
  }
  .info {
    width: 50%;
    padding: 2rem;
    .tech {
      display: flex;
      max-height: 100px;
      padding: 2rem;
      img {
        max-height: 100px;
        margin: 0 2rem;
        width: auto;
      }
    }
  }
}
